@import "sass/index";

$t-area-max-width: 390px;

.root {
  width: 100%;
  padding: 60px;
  border-radius: 15px;
  position: relative;
  color: use(--secondary-main);
  background: linear-gradient(131.49deg, #24375f 2.6%, #000000 60.63%), #0a0d18;

  @media (max-width: 900px) {
    padding: 20px;
  }

  &_white {
    background: use(--primary-main);

    & .title {
      color: use(--secondary-main);
    }

    & .form {
      border: none;
      box-shadow: none;
    }

    & .generalInput {
      color: #555477;
      border-color: #ced4da;
      background: none;
    }

    & .select {
      color: #555477;

      & div {
        color: #555477;
      }

      & svg g {
        stroke: #555477;
      }
    }

    & .textArea {
      &::placeholder {
        color: "";
        opacity: "";
      }
    }

    & .labelText {
      color: use(--secondary-main);
    }

    & .labelClass {
      color: #555477;
    }
  }
}

.box {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: flex-start;
  @media (max-width: $tabletScreen) {
    flex-direction: column;
  }
}

.title {
  font-size: 26px;
  font-weight: 600;
  line-height: 31px;
  margin-top: 0;
  text-transform: uppercase;
  color: use(--primary-contrastText);
}

.textBox {
  max-width: 428px;
  margin-right: 10px;
  @media (max-width: $tabletScreen) {
    max-width: 100%;
  }
}

.text {
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  mix-blend-mode: normal;
  opacity: 0.56;
}

.form {
  height: min-content;
  padding: 4px 20px 20px 20px;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );

  border: 1px solid rgba(72, 53, 234, 0.572962);
  box-shadow: inset 0px -10000px 0px rgba(72, 53, 234, 0.17783),
    0px 0px 24px rgba(72, 53, 234, 0.196487);
  border-radius: 8px;
}

.generalInput {
  width: 100%;
  height: 40px;

  color: use(--primary-contrastText);
  margin-top: 20px;
  border-radius: 8px;
  font-family: "Gilroy";
  font-size: 14px;
  line-height: 16px;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.inputRoot {
  margin-top: 20px;
}

.input {
  & > input::placeholder {
    font-weight: 400;
    font-size: 14px;
    opacity: 0.5;
  }
}

.select {
  padding-right: 5px;
  padding-left: 10px;
  &Icon {
    transform: scale(0.6);
  }
  &Label {
    & > span {
      font-weight: 600;
      font-size: 14px;
    }
  }
}

.textArea {
  margin: 0;
  min-height: 150px;
  padding: 10px;

  resize: none;
  outline: none;

  max-width: $t-area-max-width;
  min-width: 200px;

  &::placeholder {
    color: use(--primary-contrastText);
    opacity: 0.5;
    font-weight: 400;
    font-size: 14px;
  }
  @media (max-width: $tabletScreen) {
    max-width: 100%;
  }
}

.label {
  margin-top: 36px;
  position: relative;
}

.labelText {
  color: use(--primary-contrastText);
  font-size: 13px;
  font-weight: 500;
  position: absolute;
  top: 0;
  transform: translateY(-110%);
}

.captchaWrapper {
  height: max-content;
  width: max-content;
  margin: 16px auto;
  position: relative;

  @media (max-width: 370px) {
  }
}

.button {
  margin: 16px auto 0;
  width: 60%;
  min-width: 200px;
  height: 50px;
  display: block;
  border: 1px solid #ced4da;
  border-radius: 25px;
  color: white;
  font-size: 20px;
  line-height: 24px;

  &:disabled {
    background-color: transparent;
    color: #555477;
  }
}

.buttonDark {
  border: none;
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  color: use(--primary-contrastText);
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.135107);

  &:disabled {
    opacity: 0.3;
    color: use(--primary-contrastText);
  }
}

.labelClass {
  color: use(--primary-contrastText);
  font-size: 13px;
  font-weight: 400;
}

.selectLabel {
  color: use(--primary-contrastText);
}
